.people-data-display {
    width: 1350px;
    display: flex;
    flex-wrap: wrap;

    > div {
        width: 235px;
        margin-top: 30px;
        margin-right: 35px;
        box-shadow: 0 2px 8px rgba(0,0,0,0.1);
        border: 1px solid rgba(227, 227, 227, 1);

        > a {
            > img {
                width: 100%;
                height: 235px;
                object-fit: cover;
                cursor: pointer;
            }

            > .no-image {
                width: 100%;
                height: 235px;
                background-color: #DBDBDB;
                display: flex;
                justify-content: center;
                align-items: center;
                cursor: pointer;

                > img {
                    width: 120px;
                    height: 120px;
                    object-fit: cover;
                }
            }
        }

        > div {
            padding: 2px 10px 8px 10px;

            > a {
                text-decoration: none;

                > h3 {
                    display: inline;
                    font-size: 18px;
                    font-weight: 600;
                    color: black;
                }
            }

            > p {
                font-size: 14.5px;
                font-weight: 400;
                color: rgba(0,0,0,0.6);
            }
        }
    }
}

@media only screen and (max-width: 1380px) {
    .people-data-display {
        width: 1080px;
    }
}

@media only screen and (max-width: 1115px) {
    .people-data-display {
        width: 810px;
    }
}

@media only screen and (max-width: 850px) {
    .people-data-display {
        width: 540px;
    }
}

@media only screen and (max-width: 570px) {
    .people-data-display {
        width: 235px;

        > div {
            margin-right: 0;
        }
    }
}